import React, { Component, useState } from "react";
import { useEffect } from "react";
import { connect, useDispatch } from "react-redux";
import { Switch, Route } from "react-router-dom";
import { APICallbackTable } from "../api";
import { FarmSpending, FarmWorker } from "../Farm/FarmItem";
import { FieldMaladieItem } from "../Farm/SmallComponent";
import { toggleNetworkCall, toggleSelectedField } from "../Redux/Store";
import {
  CardWithChart,
  ModalComponent,
  SelectAOption,
  NoDataDisplay,
  AmountDemostraction,
} from "../Reuse/Reuse.jsx";
import "./FieldItem.css";

function FieldTransactionSaleItem({ data }) {
  const { entry = {}, quantity, pu, pg } = data;
  return (
    <div className="FieldTransactionSaleItem">
      <div className="FieldTransactionSaleItem_Header flex">
        <p>Store: {entry?.store.name}</p>
        <p>Produit: {entry?.product.name}</p>
      </div>
      <div>
        <p>
          Quantit: {quantity} {entry?.mesure?.name}
        </p>
        <p>Prix Unitaire: {pu} Fbu</p>
        <p>Prix Globale: {pg} Fbu</p>
      </div>
    </div>
  );
}
function FieldTransactionItem({ data }) {
  const { creator, client, created, sales = [] } = data;
  return (
    <div className="FieldTransactionItem margin">
      <div className="FieldTransactionItem_Header flex">
        <p>{creator.name}</p>
        <p>{client}</p>
        <p>{new Date(created).toLocaleString()}</p>
      </div>
      <div className="FieldTransactionItem_Body">
        {sales.map((Item) => (
          <FieldTransactionSaleItem key={Item.id} data={Item} />
        ))}
      </div>
    </div>
  );
}
function FieldTransaction({ data }) {
  return (
    <>
      {!data?.length && (
        <NoDataDisplay message="Aucune Transaction Trouve !" />
      )}
      {data?.map((Item) => (
        <FieldTransactionItem key={Item.id} data={Item} />
      ))}
    </>
  );
}
function FieldGraphicSpending({ Id }) {
  return (
    <div className="margin">
      <CardWithChart
        calculator={(item) => item.amount}
        height={100}
        title="Montant Total De Charge"
        url={`charges_champ/?callback=Statistic&Entity=${Id}`}
      />
    </div>
  );
}
function FieldGraphicIncome({ Id }) {
  return (
    <div className="margin">
      <CardWithChart
        calculator={(item) => item.sales.reduce((n, item) => n + item.pg, 0)}
        height={100}
        title="Montant Total De Revenu"
        url={`transactions_field/?callback=Statistic&Sales__Stock__Store__Field=${Id}`}
      />
    </div>
  );
}
function FieldSickness({ Id }) {
  const dispatch = useDispatch();
  const [List, setList] = useState([]);

  useEffect(() => {
    dispatch(toggleNetworkCall());
    APICallbackTable({
      url: `champ_fiche_maladie/?callback=Fetch&Entity=${Id}`,
      default_response: [],
    })
      .then((data) => setList(data))
      .finally(() => dispatch(toggleNetworkCall()));
  }, []);

  return (
    <>
      {!List?.length && <NoDataDisplay message="Fiche Maladie Vide !" />}
      {List.map((Item) => (
        <FieldMaladieItem key={Item.id} data={Item} />
      ))}
    </>
  );
}
function FieldView({ data = [] }) {
  const { id, transaction, workers, charges, family } = data;
  return (
    <>
      <Switch>
        <Route path="/Champ/Workers">
          <FarmWorker data={workers} />
        </Route>
        <Route path="/Champ/Spending">
          <FarmSpending data={charges} />
        </Route>
        <Route path="/Champ/Transaction">
          <FieldTransaction data={transaction} />
        </Route>
        <Route path="/Champ/GraphicIncome">
          <FieldGraphicIncome Id={id} />
        </Route>
        <Route path="/Champ/GraphicSpending">
          <FieldGraphicSpending Id={id} />
        </Route>
        <Route path="/Champ/Maladie">
          <FieldSickness Id={id} />
        </Route>
        <Route path="/Champ/Somme">
          <AmountDemostraction Id={id} Entity="champ_terrain" />
        </Route>
        <Route>
          <SelectAOption />
        </Route>
      </Switch>
    </>
  );
}
class FieldItem extends Component {
  toggleView = () => this.props.dispatch(toggleSelectedField({}));

  render() {
    return (
      <ModalComponent
        title={`Detail Du Champ ${this.props.SelectedField.name}`}
        show={this.props.SelectedField.id}
        toggleView={this.toggleView}
        tabs={[
          { url: "/Champ/Workers", label: "Travailleur" },
          { url: "/Champ/Transaction", label: "Transaction" },
          { url: "/Champ/Spending", label: "Dpense" },
          { url: "/Champ/GraphicIncome", label: "Revenue Chart" },
          { url: "/Champ/GraphicSpending", label: "Dpense Chart" },
          { url: "/Champ/Maladie", label: "Fiche Maladie" },
          { url: "/Champ/Somme", label: "Somme" },
        ]}
      >
        <FieldView data={this.props.SelectedField} />
      </ModalComponent>
    );
  }
}
export default connect(
  (state) => ({ SelectedField: state.Utils.SelectedField }),
  null
)(FieldItem);
