import { useEffect, useState, memo } from "react";
import { useDispatch } from "react-redux";
import { APIViewOne, APIFunctionCallback } from "../api";
import { toggleNetworkCall, toggleSelectedAnimal } from "../Redux/Store";
import { NoDataDisplay } from "../Reuse/Reuse";
import "./SmallComponent.css";

function SaleItem({ data: { animal, price } }) {
  return (
    <div className="FarmTransactionItemSaleItem flex">
      <div className="">
        <p>
          {animal.category} / Animal: {animal.code}
        </p>
      </div>
      <div>{price} Fbu</div>
    </div>
  );
}
export function FarmTransactionItem({ data = {} }) {
  const { client, created, animals } = data;
  return (
    <div className="FarmTransactionItem">
      <div className="FarmTransactionItemHeader flex">
        <p>{client}</p>
        <p>{new Date(created).toLocaleString()}</p>
      </div>
      <div className="FarmTransactionItemBody">
        {animals.map((Item) => (
          <SaleItem key={Item.id} data={Item} />
        ))}
      </div>
      <div className="FarmTransactionItemFooter flex">
        <p>Total</p>
        <p className="FarmTransactionItemFooter_Total">
          {animals.reduce((n, item) => n + item.price, 0)} Fbu
        </p>
      </div>
    </div>
  );
}
export function FarmWorkerItem({ data = {} }) {
  const { name, phone, image, address, role } = data;
  return (
    <div className="FarmWorkerItem flex margin">
      <div>
        <img src={image} />
      </div>
      <div className="FarmWorkerItemDetail">
        <p>{name}</p>
        <p className="FarmWorkerItemPhone">
          <i class="fa-solid fa-user-doctor"></i> {role}
        </p>
        <p className="FarmWorkerItemPhone">
          <i className="fas fa-phone"></i> {phone}
        </p>
        <p className="FarmWorkerItemAddress">
          <i className="fa-solid fa-location-dot"></i> {address}
        </p>
      </div>
    </div>
  );
}
export function FarmSpendItem({ data = {} }) {
  const { amount, created, raison } = data;
  return (
    <div className="FarmSpendItem">
      <div className="FarmSpendItemHeader flex">
        <p className="FarmSpendItem_Amount">{amount} Fbu</p>
        <p>{new Date(created).toLocaleString()}</p>
      </div>
      <div className="FarmSpendItemBody">
        <p>{raison}</p>
      </div>
    </div>
  );
}
function AnimalItemPresentation({ data = {} }) {
  const { code, image, available = true, sickeness, outraison } = data;
  const dispatch = useDispatch();
  const Publish = () => dispatch(toggleSelectedAnimal(data));

  return (
    <div
      className="AnimalItemPresentation flex"
      onClick={(e) => {
        e.preventDefault();
        Publish();
      }}
    >
      <div className="AnimalItemPresentation_Header flex">
        <img src={image} className={!available ? "animal_unavailable" : ""} />
        <div>
          <p>Code: {code}</p>
          <p>Etat: {outraison}</p>
        </div>
      </div>
      <div>
        <p>Historique Maladie: {sickeness?.sicklist?.length || 0}</p>
      </div>
    </div>
  );
}
export const FamilyAnimal = memo(function ({ data = {} }) {
  const { category, animals, code } = data;
  return (
    <div className="FamilyAnimalItem">
      <div className="FamilyAnimalItemHeader flex">
        <p>
          {category.name} / {code}
        </p>
        <p>Number: {animals.length}</p>
      </div>
      <div>
        {animals.map((Item) => (
          <AnimalItemPresentation key={Item.id} data={Item} />
        ))}
      </div>
    </div>
  );
});
function AnimalSickItem({ data = {}, onUpdate = () => {}, Sheet = {} }) {
  const { id, sick, detail, healed, heal_at, sick_at } = data;
  const toggleSickState = ({ target }) => {
    let value = Number(target.checked);
    APIFunctionCallback({
      url: `maladie_history/${id}/?callback=HealedToggle&Heal=${value}`,
    }).then(() => onUpdate(Sheet));
  };
  return (
    <div className="AnimalSickItem">
      <div className="AnimalSickItemHeader flex">
        <p>{sick?.name}</p>
        <input
          type="checkbox"
          defaultChecked={healed}
          onChange={toggleSickState}
        />
      </div>
      <div className="AnimalSickItemBody">{detail}</div>
      <div className="AnimalSickItemFooter">
        <p>
          Sign le <span>{new Date(sick_at).toLocaleString()}</span>
        </p>
        <p>
          Geurit le{" "}
          <span>{heal_at ? new Date(heal_at).toLocaleString() : "------"}</span>
        </p>
      </div>
    </div>
  );
}
export const AnimalHistorySickeness = memo(function ({ SheetId }) {
  const dispatch = useDispatch();
  const [List, setList] = useState([]);

  useEffect(() => {
    if (SheetId) return FetchSheet(SheetId);
    setList([]);
  }, [SheetId]);

  function FetchSheet(SheetId) {
    if (SheetId) {
      dispatch(toggleNetworkCall());
      APIViewOne({ url: `malades/${SheetId}/`, default_response: {} })
        .then((data) => {
          setList(data.sicklist);
        })
        .finally(() => dispatch(toggleNetworkCall()));
    }
  }
  return (
    <div>
      <div className="AnimalDetailTitle">
        <p>Historique Maladie</p>
      </div>
      <div>
        {!List?.length && <NoDataDisplay />}
        {List?.map((item) => (
          <AnimalSickItem
            key={item.id}
            onUpdate={FetchSheet}
            Sheet={SheetId}
            data={item}
          />
        ))}
      </div>
    </div>
  );
});
export const AnimalHistoryDead = memo(function ({ Dead = {} }) {
  const { id, created, detail } = Dead;
  return (
    <div>
      <div className="AnimalDetailTitle">
        <p>Historique Dcs</p>
      </div>
      <div className="AnimalDetailBody">
        {!id ? (
          <NoDataDisplay />
        ) : (
          <>
            <p className="AnimalHistoryDead_Created">
              Sign le {new Date(created).toLocaleString()}
            </p>
            <p>{detail}</p>
          </>
        )}
      </div>
    </div>
  );
});
export const AnimalHistorySale = memo(function ({ Sale = {} }) {
  const { id, created, price } = Sale;
  return (
    <div>
      <div className="AnimalDetailTitle">
        <p>Historique Transaction</p>
      </div>
      <div className="AnimalDetailBody">
        {!id ? (
          <NoDataDisplay />
        ) : (
          <>
            <p className="AnimalHistoryDead_Created">
              Sign le {new Date(created).toLocaleString()}
              <br />
              Prix: {price} Fbu
            </p>
          </>
        )}
      </div>
    </div>
  );
});
function ExploitationItemSale({ data }) {
  const { animal, pu, quantity, pg, mesure, product, detail } = data;
  return (
    <div className="ExploitationItemSale">
      <div className="ExploitationItemSale_Body flex">
        <div>
          <p>Categorie: {animal.category}</p>
          <p>Animal: {animal.code}</p>
        </div>
        <div>
          <p>Produit: {product.name}</p>
        </div>
        <div>
          <p>
            Quantit: {quantity} {mesure.name}
          </p>
          <p>Prix Unitaire: {pu} Fbu</p>
          <div className="ExploitationItemSale_Money">
            <p>{pg} Fbu</p>
          </div>
        </div>
      </div>
      <div className="ExploitationItemSale_Footer">
        <p>{detail}</p>
      </div>
    </div>
  );
}
export function ExploitationItem({ data }) {
  const { client, creator, sales, created } = data;
  return (
    <div className="ExploitationItem margin">
      <div className="ExploitationItem_Header flex">
        <p>Vendeur: {creator.name}</p>
        <p>Client: {client}</p>
        <p>Date: {new Date(created).toLocaleString()}</p>
      </div>
      <div className="ExploitationItem_Body">
        {sales.map((Item) => (
          <ExploitationItemSale key={Item.id} data={Item} />
        ))}
      </div>
    </div>
  );
}
export function AlimentationItem({ data = {} }) {
  const {
    animal,
    start_periode,
    end_periode,
    detail,
    created,
    mesure,
    product,
    quantity = 0,
    quality = { name: "----------" },
  } = data;
  return (
    <div className="AlimentationItem">
      <div className="AlimentationItem_Header flex">
        <p>{animal.name}</p>
        <p>
          {new Date(start_periode).toLocaleDateString()} -{" "}
          {new Date(end_periode).toLocaleDateString()}
        </p>
        <p>{new Date(created).toLocaleString()}</p>
      </div>

      <div className="AlimentationItem_Body">
        <p>Produit Consomm: {product?.name}</p>
        <p>
          Quantit: {quantity} {mesure?.name}
        </p>
        <p>Qualit: {quality?.name}</p>
      </div>

      <div className="AlimentationItem_Footer">
        <p>{detail}</p>
      </div>
    </div>
  );
}
export function FieldMaladieItem({ data = {} }) {
  const {
    creator,
    sick,
    touched_area,
    healed,
    healed_at,
    created,
    sickness_product,
  } = data;
  return (
    <div className={`FieldMaladieItem ${healed && "FieldMaladieItemHealed"}`}>
      <div className="FieldMaladieItem_Header flex">
        <p>Enregistreur: {creator?.name}</p>
        <p>Surface Touche: {touched_area}%</p>
        <p>Maladie: {sick?.name}</p>
      </div>

      <div>
        <p>Produit Touch: {sickness_product?.name}</p>
        <p>
          Enregistr les:{" "}
          {created ? new Date(created).toLocaleString() : "-----------"}
        </p>
        <p>
          Gurri:{" "}
          {healed_at ? new Date(healed_at).toLocaleDateString() : "-----------"}
        </p>
      </div>
    </div>
  );
}
