import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import APIView from "../api";
import "./Edit.css";
import {
  AdminForm,
  FarmForm,
  FieldForm,
  FishPondForm,
  MesureForm,
  PlaceForm,
  TreeForm,
  ExploitationProductForm,
  TreeStateForm,
  SickFieldForm,
  FishTypeForm,
  CategoryAnimalForm,
  FarmSickTypeForm,
  FarmAlimentationQuality,
  FarmAlimentationProduct,
  SuperifieTypeForm,
} from "../Form/Form.jsx";
import { toggleNetworkCall, toggleSelectedEditForm } from "../Redux/Store";
import { ErrorBoundnary } from "../Reuse/Reuse";

function NavLink({ label = "Admin", query = "" }) {
  const dispatch = useDispatch();
  return (
    <div
      className="NavLink flex"
      onClick={(e) => {
        e.preventDefault();
        dispatch(toggleSelectedEditForm({ label, query }));
      }}
    >
      <i className="fas fa-pen"></i>
      <p>{label}</p>
    </div>
  );
}

function FormLink() {
  return (
    <div id="edit_party_one" className="scroll">
      <NavLink label="Admin" query="Admin" />
      <NavLink label="Place" query="Place" />
      <NavLink label="Ferme" query="Farm" />
      <NavLink label="Champ" query="Field" />
      <NavLink label="Etang" query="Etang" />
      <NavLink
        label="Unit De Mesure Superficie ( Champ )"
        query="MesureArea"
      />
      <NavLink
        label="Unit De Mesure Produit ( Champ )"
        query="MesureProduit"
      />
      <NavLink label="Type Champ Produit ( Champ )" query="Tree" />
      <NavLink label="Produit A Exploiter" query="ExploitationProduct" />
      <NavLink label="Etat D'Un Champ" query="TreeState" />
      <NavLink label="Maladie Touchant Un Champ" query="SickField" />
      <NavLink label="Type De Poisson" query="FishType" />
      <NavLink label="Type D'Animaux De La Ferme" query="AnimalCategory" />
      <NavLink label="Maladie Touchant Une Ferme" query="FarmSickType" />
      <NavLink
        label="Ferme ( Produit Consommable )"
        query="FarmProductAlimentation"
      />
      <NavLink
        label="Ferme ( Qualit Alimentaire )"
        query="FarmQualityAlimenation"
      />
    </div>
  );
}

function FormLogic() {
  const SelectedForm = useSelector(
    (state) => state.Utils.SelectedEditForm || {}
  );
  const dispatch = useDispatch();
  const [meta, setMeta] = useState({});

  const GetMetaData = () => {
    dispatch(toggleNetworkCall());
    APIView({ url: "meta_data/", default_response: {} })
      .then((data) => setMeta(data))
      .finally(() => dispatch(toggleNetworkCall()));
  };

  useEffect(() => GetMetaData(), []);

  const Forms = {
    Admin: <AdminForm />,
    Place: <PlaceForm />,
    Farm: <FarmForm meta={meta} />,
    Field: <FieldForm meta={meta} />,
    Etang: <FishPondForm meta={meta} />,
    MesureArea: <SuperifieTypeForm />,
    MesureProduit: <MesureForm />,
    Tree: <TreeForm />,
    ExploitationProduct: <ExploitationProductForm />,
    TreeState: <TreeStateForm meta={meta} />,
    SickField: <SickFieldForm meta={meta} />,
    AnimalCategory: <CategoryAnimalForm />,
    FishType: <FishTypeForm />,
    FarmSickType: <FarmSickTypeForm />,
    FarmQualityAlimenation: <FarmAlimentationQuality />,
    FarmProductAlimentation: <FarmAlimentationProduct meta={meta} />,
  };

  return (
    <div id="edit_party_two">
      <div id="formContenairHeader" className="sticky">
        <p>Formulaire: {SelectedForm.label}</p>
      </div>
      <div className="flex">
        <div id="formContenairObject">
          {SelectedForm.query ? (
            Forms[SelectedForm.query]
          ) : (
            <p>Aucun Formulaire N'est Selectionner !</p>
          )}
        </div>
      </div>
    </div>
  );
}

export default function Edit() {
  const dispatch = useDispatch();
  useEffect(() => dispatch(toggleSelectedEditForm()));

  return (
    <ErrorBoundnary>
      <div id="Edit">
        <FormLink />
        <FormLogic />
      </div>
    </ErrorBoundnary>
  );
}
