import { Route, Switch } from "react-router-dom";
import welcomeIcon from "./welcome_icon.png";
import React, { Component } from "react";
import Etang from "../Etang/Etang";
import Field from "../Field/Field";
import Edit from "../Edit/Edit";
import Farm from "../Farm/Farm";
import { ErrorBoundnary, SelectAOption } from "../Reuse/Reuse";
import "./Body.css";

function WelcomeComponent() {
  return (
    <div id="WelcomeComponent" className="flex">
      <div id="WelcomeComponent_Contenair" className="flex">
        <div>
          <h4>Bienvenu Chez MadosGroup</h4>
          <p>
            We are team of talanted IT experts dedicated to delivering It
            solutions for Everyone. With a team of software developers and
            hardware technicians dedicated to meeting your helping improve your
            comunication and your daily activities
          </p>
        </div>
        <img src={welcomeIcon} alt="icon home" />
      </div>
      <p id="WelcomeComponent_FootText">
        <a href="https://www.madosgroup.com/" target="_blank">
          Mados Group
        </a>
      </p>
    </div>
  );
}
export default class Body extends Component {
  render() {
    return (
      <ErrorBoundnary>
        <div id="main_body">
          <Switch>
            <Route exact path="/" component={WelcomeComponent} />
            <Route path="/Farm" component={Farm} />
            <Route path="/Champ" component={Field} />
            <Route path="/Etang" component={Etang} />
            <Route path="/Edit" component={Edit} />
            <Route path="/*">
              <SelectAOption />
            </Route>
          </Switch>
        </div>
      </ErrorBoundnary>
    );
  }
}
