import React, { Component, useEffect, useRef, useState, memo } from "react";
import { connect } from "react-redux";
import { APIViewMulti } from "../api";
import { toggleSelectedFarm } from "../Redux/Store";
import { Chips, ErrorBoundnary, NoDataDisplay } from "../Reuse/Reuse.jsx";
import { FarmOverView } from "./FarmItem";
import "./Farm.css";

function UserItem({ data = {} }) {
  const { email, first_name, last_name, last_login } = data;
  return (
    <div className="flex userItem">
      <div>
        <i className="fas fa-user"></i>
      </div>
      <div className="flex userItemDetail">
        <div>
          <p className="userItemName">{`${first_name} ${last_name}`}</p>
          <p className="userItemEmail">{email}</p>
        </div>
        <div>
          <p className="userItemLogin">
            {last_login
              ? new Date(last_login).toLocaleString()
              : "------------"}
          </p>
        </div>
      </div>
    </div>
  );
}
function UserList({ extra = {} }) {
  const [List, setList] = useState([]);
  const Ref = useRef();

  useEffect(() => {
    Ref?.current?.classList?.toggle("selfLoader");
    APIViewMulti({ url: "personnes/", default_response: [] })
      .then((data) => setList(data))
      .finally(() => {
        Ref?.current?.classList?.toggle("selfLoader");
      });
  }, []);
  return (
    <ErrorBoundnary>
      <div className="farm_party scroll" ref={Ref} {...extra}>
        <p className="sticky list_admin_area">Administrateurs</p>
        {!List?.length && <NoDataDisplay />}
        {List?.map((Item) => (
          <UserItem key={Item.email} data={Item} />
        ))}
      </div>
    </ErrorBoundnary>
  );
}
function PlaceItem({ data = {}, Filter = () => [], onToggle = () => {} }) {
  const { image, name } = data;
  const ItemList = Filter(data);

  const onClick = (Id) => {
    const Target = ItemList.find((item) => item.id === Id);
    onToggle(Target);
  };

  return (
    <>
      <div className="PlaceItem flex">
        <div className="PlaceItemImageContenair flex">
          <img src={image} alt="Image De La Place" />
        </div>
        <div className="PlaceItemDetailContenair flex">
          <div>
            <p className="PlaceItemName">{name}</p>
            <div className="flex PlaceItemFarmList">
              {ItemList?.map((item) => (
                <Chips
                  key={item.id}
                  id={item.id}
                  title={item.name}
                  onClick={onClick}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
export const PlaceList = memo(({ Filter, onToggle, extra }) => {
  const [List, setList] = useState([]);
  const Ref = useRef();

  useEffect(() => {
    Ref.current?.classList?.add("selfLoader");
    APIViewMulti({ url: "places/", default_response: [] })
      .then((data) => setList(data))
      .finally(() => {
        Ref.current?.classList?.remove("selfLoader");
      });
  }, []);

  return (
    <ErrorBoundnary>
      <div className="farm_party scroll" ref={Ref} {...extra}>
        {!List?.length && <NoDataDisplay message="Aucune Ville Trouve !" />}
        {List?.map((Item) => (
          <PlaceItem
            key={Item.id}
            data={Item}
            Filter={Filter}
            onToggle={onToggle}
          />
        ))}
      </div>
    </ErrorBoundnary>
  );
});

class Farm extends Component {
  FilterFarm = ({ farms }) => farms;
  ToggleFarm = (data) => this.props.dispatch(toggleSelectedFarm(data));

  render() {
    return (
      <ErrorBoundnary>
        <div id="farm">
          <UserList extra={{ id: "farm_party_two" }} />
          <PlaceList
            Filter={this.FilterFarm}
            onToggle={this.ToggleFarm}
            extra={{ id: "farm_party_one" }}
          />
          <FarmOverView />
        </div>
      </ErrorBoundnary>
    );
  }
}
export default connect(null)(Farm);
