import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Route, Switch } from "react-router-dom";
import {
  toggleSelectedAnimal,
  toggleSelectedFarm,
  toggleNetworkCall,
} from "../Redux/Store";
import {
  AnimalHistoryDead,
  AnimalHistorySale,
  AnimalHistorySickeness,
  ExploitationItem,
  FamilyAnimal,
  FarmSpendItem,
  FarmTransactionItem,
  FarmWorkerItem,
} from "./SmallComponent.jsx";
import {
  CardWithChart,
  Alimentation,
  ModalComponent,
  SelectAOption,
  NoDataDisplay,
  AmountDemostraction,
  ErrorBoundnary,
} from "../Reuse/Reuse.jsx";
import { APIViewMulti } from "../api";
import "./FarmItem.css";

function FarmTransaction({ data = [] }) {
  return (
    <>
      {!data?.length && <NoDataDisplay />}
      {data.map((Item) => (
        <FarmTransactionItem key={Item.id} data={Item} />
      ))}
    </>
  );
}
export function FarmWorker({ data = [] }) {
  return (
    <>
      {!data?.length && <NoDataDisplay />}
      {data.map((Item) => (
        <FarmWorkerItem key={Item.id} data={Item} />
      ))}
    </>
  );
}
export function FarmSpending({ data = [] }) {
  return (
    <>
      {!data?.length && <NoDataDisplay />}
      {data?.map((Item) => (
        <FarmSpendItem key={Item.id} data={Item} />
      ))}
    </>
  );
}
function FarmAnimal({ data }) {
  const dispatch = useDispatch();
  const {
    sickeness = {},
    dead = {},
    saled = {},
  } = useSelector((state) => state.Utils.SelectedAnimal);

  useEffect(() => {
    return () => dispatch(toggleSelectedAnimal({}));
  }, []);

  return (
    <div id="FarmAnimalField" className="margin">
      <div id="FarmAnimalListField" className="scroll">
        {!data?.length && <NoDataDisplay />}
        {data.map((Item) => (
          <FamilyAnimal key={Item.id} data={Item} />
        ))}
      </div>
      <div id="FarmAnimalDetailField" className="scroll">
        <ErrorBoundnary>
          <AnimalHistorySickeness SheetId={sickeness?.id} />
        </ErrorBoundnary>
        <ErrorBoundnary>
          <AnimalHistoryDead Dead={dead} />
        </ErrorBoundnary>
        <ErrorBoundnary>
          <AnimalHistorySale Sale={saled} />
        </ErrorBoundnary>
      </div>
    </div>
  );
}
function FarmGraphicIncome({ Id }) {
  return (
    <div className="margin">
      <CardWithChart
        calculator={(item) =>
          item.animals.reduce((n, item) => n + item.price, 0)
        }
        height={100}
        title="Revenu Total"
        url={`transactions_farm/?callback=Statistic&Farm=${Id}`}
      />
    </div>
  );
}
function FarmGraphicSpending({ Id }) {
  return (
    <div className="margin">
      <CardWithChart
        calculator={(item) => item.amount}
        height={100}
        title="Dpense Totale"
        url={`charges_farm/?callback=Statistic&Entity=${Id}`}
      />
    </div>
  );
}
function FarmGraphicExploitation({ Id }) {
  return (
    <div className="margin">
      <CardWithChart
        calculator={(item) =>
          item.sales.reduce((init, sale) => init + sale.pg, 0)
        }
        height={100}
        title="Exploitation Totale"
        url={`transaction_exploitation/?callback=Statistic&Farm=${Id}`}
      />
    </div>
  );
}
function FarmExploitation({ data }) {
  return (
    <>
      {!data?.length && <NoDataDisplay />}
      {data?.map((Item) => (
        <ExploitationItem key={Item.id} data={Item} />
      ))}
    </>
  );
}
function FarmAlimentation({ Id }) {
  const dispatch = useDispatch();
  const [animalType, setAnimalType] = useState([]);

  const URLGenerator = ({ startDate, endDate, selectedType }) => {
    return `ferme_alimentation/?callback=Fetch&Farm=${Id}&Product__Animal=${selectedType}&StartPeriode__gte=${startDate}&EndPeriode__lte=${endDate}`;
  };

  useEffect(() => {
    dispatch(toggleNetworkCall());
    APIViewMulti({
      url: "typeanimaux/",
      default_response: [],
    })
      .then((data) => setAnimalType(data))
      .finally(() => dispatch(toggleNetworkCall()));
  }, []);
  return <Alimentation ListTypes={animalType} URLGenerator={URLGenerator} />;
}

function FarmView({ data }) {
  const { id, transaction, workers, spending, family, exploitation } = data;
  return (
    <>
      <Switch>
        <Route path="/Farm/Transaction">
          <FarmTransaction data={transaction} />
        </Route>
        <Route path="/Farm/Workers">
          <FarmWorker data={workers} />
        </Route>
        <Route path="/Farm/Spending">
          <FarmSpending data={spending} />
        </Route>
        <Route path="/Farm/Animal">
          <FarmAnimal data={family} />
        </Route>
        <Route path="/Farm/GraphicIncome">
          <FarmGraphicIncome Id={id} />
        </Route>
        <Route path="/Farm/GraphicSpending">
          <FarmGraphicSpending Id={id} />
        </Route>
        <Route path="/Farm/GraphicExploitation">
          <FarmGraphicExploitation Id={id} />
        </Route>
        <Route path="/Farm/Exploitation">
          <FarmExploitation data={exploitation} />
        </Route>
        <Route path="/Farm/Alimentation">
          <FarmAlimentation Id={id} />
        </Route>
        <Route path="/Farm/SommeTotal">
          <AmountDemostraction Id={id} Entity="fermes" />
        </Route>
        <Route>
          <SelectAOption />
        </Route>
      </Switch>
    </>
  );
}
export function FarmOverView() {
  const dispatch = useDispatch();
  const farmItem = useSelector((state) => state.Utils.SelectedFarm);
  const toggleView = () => dispatch(toggleSelectedFarm({}));

  return (
    <ModalComponent
      title={`Detail Sur La Ferme ${farmItem.name}`}
      show={farmItem.id}
      toggleView={toggleView}
      tabs={[
        { url: "/Farm/Workers", label: "Travailleur" },
        { url: "/Farm/Transaction", label: "Transaction" },
        { url: "/Farm/Spending", label: "Dpense" },
        { url: "/Farm/Animal", label: "Animal" },
        { url: "/Farm/GraphicIncome", label: "Transaction(Chart)" },
        { url: "/Farm/GraphicSpending", label: "Dpense(Chart)" },
        { url: "/Farm/GraphicExploitation", label: "Exploitation(Chart)" },
        { url: "/Farm/Exploitation", label: "Exploitation" },
        { url: "/Farm/Alimentation", label: "Alimentation" },
        { url: "/Farm/SommeTotal", label: "Somme" },
      ]}
    >
      <FarmView data={farmItem} />
    </ModalComponent>
  );
}
