import { Component, useState, useEffect } from "react";
import { connect, useDispatch } from "react-redux";
import { Route } from "react-router-dom";
import { Switch } from "react-router-dom";
import { FarmSpending, FarmWorker } from "../Farm/FarmItem";
import { toggleSelectedEtang, toggleNetworkCall } from "../Redux/Store";
import {
  CardWithChart,
  ModalComponent,
  SelectAOption,
  Alimentation,
  NoDataDisplay,
  AmountDemostraction,
} from "../Reuse/Reuse";
import { APIViewMulti } from "../api";
import "./EtangItem.css";

function EtangAlimentation({ Id }) {
  const dispatch = useDispatch();
  const [fishType, setFishType] = useState([]);

  const URLGenerator = ({ startDate, endDate, selectedType }) => {
    return `etang_alimentation/?callback=Fetch&FishPond=${Id}&Product__Fish=${selectedType}&StartPeriode__gte=${startDate}&EndPeriode__lte=${endDate}`;
  };

  useEffect(() => {
    dispatch(toggleNetworkCall());
    APIViewMulti({
      url: "poissons_type/",
      default_response: [],
    })
      .then((data) => setFishType(data))
      .finally(() => dispatch(toggleNetworkCall()));
  }, []);
  return <Alimentation ListTypes={fishType} URLGenerator={URLGenerator} />;
}
function EtangTransactionItem({ data = {} }) {
  const { client, created, fish, pu, quantity, pg, mesure } = data;
  return (
    <div className="EtangTransactionItem margin">
      <div className="EtangTransactionItem_Header flex">
        <p>{client}</p>
        <p>{new Date(created).toLocaleString()}</p>
      </div>
      <div className="EtangTransactionItem_Body">
        <p>
          Product: <span>{fish?.name}</span>
        </p>
        <p>
          Quantit:{" "}
          <span>
            {quantity} {mesure?.name}
          </span>
        </p>
        <p>
          Prix Unitaire: <span>{pu} Fbu</span>
        </p>
        <p>
          Prix Global:{" "}
          <span className="EtangTransactionItem_Body_PG">{pg} Fbu</span>
        </p>
      </div>
    </div>
  );
}
function EtangTransaction({ data }) {
  return (
    <>
      {!data?.length && <NoDataDisplay />}
      {data?.map((Item) => (
        <EtangTransactionItem key={Item.id} data={Item} />
      ))}
    </>
  );
}
function EtangGraphicIncome({ Id }) {
  return (
    <div className="margin">
      <CardWithChart
        calculator={(item) => item.pg}
        height={100}
        title="Montant Revenu"
        url={`ventes_poissons/?callback=Statistic&FishPond=${Id}`}
      />
    </div>
  );
}
function EtangGraphicSpending({ Id }) {
  return (
    <div className="margin">
      <CardWithChart
        calculator={(item) => item.amount}
        height={100}
        title="Montant Charge"
        url={`charges_etang/?callback=Statistic&Entity=${Id}`}
      />
    </div>
  );
}
function EtangView({ data = [] }) {
  const { id, transactions, workers, charges } = data;
  return (
    <>
      <Switch>
        <Route path="/Etang/Workers">
          <FarmWorker data={workers} />
        </Route>
        <Route path="/Etang/Spending">
          <FarmSpending data={charges} />
        </Route>
        <Route path="/Etang/Transaction">
          <EtangTransaction data={transactions} />
        </Route>
        <Route path="/Etang/GraphicIncome">
          <EtangGraphicIncome Id={id} />
        </Route>
        <Route path="/Etang/GraphicSpending">
          <EtangGraphicSpending Id={id} />
        </Route>
        <Route path="/Etang/Alimentation">
          <EtangAlimentation Id={id} />
        </Route>
        <Route path="/Etang/Somme">
          <AmountDemostraction Id={id} Entity="etang" />
        </Route>
        <Route>
          <SelectAOption />
        </Route>
      </Switch>
    </>
  );
}

class EtangItem_ extends Component {
  toggleView = () => this.props.dispatch(toggleSelectedEtang({}));
  render() {
    return (
      <ModalComponent
        title={`Detail Sur L'Etang ${this.props.SelectedEtang.name}`}
        show={this.props.SelectedEtang.id}
        toggleView={this.toggleView}
        tabs={[
          { url: "/Etang/Workers", label: "Travailleur" },
          { url: "/Etang/Transaction", label: "Transaction" },
          { url: "/Etang/Spending", label: "Dpense" },
          { url: "/Etang/GraphicIncome", label: "Revenu Chart" },
          { url: "/Etang/GraphicSpending", label: "Dpense Chart" },
          { url: "/Etang/Alimentation", label: "Alimentation" },
          { url: "/Etang/Somme", label: "Somme" },
        ]}
      >
        <EtangView data={this.props.SelectedEtang} />
      </ModalComponent>
    );
  }
}
const EtangItem = connect(
  (state) => ({ SelectedEtang: state.Utils.SelectedEtang }),
  null
)(EtangItem_);
export default EtangItem;
