import React, { Component, memo } from "react";
import { PlaceList } from "../Farm/Farm";
import FieldItem from "./FieldItem.jsx";
import { useDispatch } from "react-redux";
import { toggleSelectedField } from "../Redux/Store";
import { ErrorBoundnary, NoDataDisplay } from "../Reuse/Reuse";
import "./Field.css";

function FieldStoreEntry({ data }) {
  const { store, product, mesure, quantity, available_quantity, created } =
    data;
  return (
    <div className="FieldStoreEntry">
      <div className="FieldStoreEntryHead flex">
        <p>
          {store.name} - {product.name}
        </p>
        <p>{new Date(created).toLocaleString()}</p>
      </div>
      <div className="FieldStoreEntryBody">
        <p>
          Quantit Entrante: {quantity} {mesure.name}
        </p>
        <p>
          Quantit Sortante: {quantity - available_quantity} {mesure.name}
        </p>
        <p>
          Quantit Restante: {available_quantity} {mesure.name}
        </p>
      </div>
    </div>
  );
}

const FieldStoreTransaction = memo(function ({ data }) {
  const { entries } = data;
  return (
    <div id="field_party_three" className="scroll">
      {!entries?.length && <NoDataDisplay />}
      {entries?.map((Item) => (
        <FieldStoreEntry key={Item.id} data={Item} />
      ))}
    </div>
  );
});

function StoreItemPresentation({ data, SelectedStore = () => {} }) {
  const { id, name } = data;
  return (
    <div className="StoreItemPresentation flex">
      <div>
        <p className="StoreItemPresentation_Id">{`${id}`.padStart(6, 0)}</p>
        <p className="StoreItemPresentation_Name">{name}</p>
      </div>
      <div>
        <i
          className="StoreItemPresentation_ViewTrans fas fa-arrow-right"
          onClick={(e) => {
            e.preventDefault();
            SelectedStore(data);
          }}
        ></i>
      </div>
    </div>
  );
}

function FieldStore({ data = {}, SelectedStore = () => {} }) {
  const {
    id,
    name = "Nom: ------------",
    created,
    image,
    superficie = "------",
    admin,
    state = { name: "------------" },
    current_product = { name: "-----------" },
    store,
  } = data;
  const dispatch = useDispatch();
  const SelectThisStore = () => dispatch(toggleSelectedField(data));
  return (
    <div id="field_party_two" className="">
      <div id="FieldStore_Head" className="flex">
        <div id="FieldStore_Head_Detail" className="flex">
          <img src={image} />
          <div>
            <p id="FieldName">{name}</p>
            <div id="FieldStore_Head_Detail_Meta">
              <p>
                Gerant:{" "}
                {admin
                  ? `${admin?.first_name} ${admin?.last_name}`
                  : "------------"}
              </p>
              <p>Superficie: {superficie}</p>
              <p>Cultiver: {current_product?.name}</p>
              <p>Etat Actuel: {state?.name}</p>
              <p>
                Date De Creation:{" "}
                {created
                  ? new Date(created).toLocaleDateString()
                  : "------------"}
              </p>
            </div>
          </div>
        </div>
        <i
          className="fas fa-eye"
          onClick={(e) => {
            e.preventDefault();
            if (id) SelectThisStore();
          }}
        ></i>
      </div>
      <div className="FieldStore_Body">
        {!store?.length && <NoDataDisplay message="Aucun Dpot Trouv !" />}
        {store?.map((item) => (
          <StoreItemPresentation
            SelectedStore={SelectedStore}
            key={item.id}
            data={item}
          />
        ))}
      </div>
    </div>
  );
}

export default class Field extends Component {
  state = { SelectedField: {}, SelectedStore: {} };
  FilterField = ({ fields }) => fields;
  ToggleField = (data) =>
    this.setState({ SelectedField: data, SelectedStore: {} });
  SelectedStore = (data) => this.setState({ SelectedStore: data });

  render() {
    return (
      <div id="Field">
        <ErrorBoundnary>
          <FieldItem />
        </ErrorBoundnary>
        <ErrorBoundnary>
          <PlaceList
            Filter={this.FilterField}
            onToggle={this.ToggleField}
            extra={{ id: "field_party_one" }}
          />
        </ErrorBoundnary>
        <ErrorBoundnary>
          <FieldStore
            SelectedStore={this.SelectedStore}
            data={this.state.SelectedField}
          />
        </ErrorBoundnary>
        <FieldStoreTransaction data={this.state.SelectedStore} />
      </div>
    );
  }
}
