import React from 'react';
import { AlertMaker, RequestLoader } from './Reuse/Reuse';
import { useDispatch, useSelector } from 'react-redux';
import { saveServerToken } from './Redux/Store';
import { Login } from './Form/Form';
import Body from './Body/Body';
import Nav from './Navbar/Nav';

function App() {
  const dispatch = useDispatch();
  const Token = useSelector(state => state.Utils.AuthToken)
  const onSuccess = token => dispatch(saveServerToken(token))

  return <>
    <AlertMaker />
    <RequestLoader />
    {
      !Token ?
      <Login onSuccess={onSuccess}/> :
      <>
      <Nav/>
      <Body />
      </>
    }
  </>
}
export default App
