import { useState } from "react";
import { useDispatch } from "react-redux";
import APIView, { APIViewMulti } from "../api";
import { addAlert, toggleNetworkCall } from "../Redux/Store";
import { promptAsk } from "../Reuse/Function";
import "./Form.css";

function Form({
  children,
  url,
  button_text = "Enregistrer",
  default_response = {
    errors: [
      {
        name: "Server",
        message: "Une Erreur Est Survenue Lors De La Reception Des Donnes !",
      },
    ],
  },
  initialState = {},
}) {
  const dispatch = useDispatch();
  var data = initialState;

  const onChange = async (e) => {
    e.preventDefault();
    var { name, value } = e.target;

    if (e.target.type === "file") {
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      formData.append("upload_preset", "farmReact");

      dispatch(toggleNetworkCall());
      await fetch("https://api.cloudinary.com/v1_1/mfarm/image/upload", {
        method: "POST",
        body: formData,
      })
        .then(async (data) => {
          const { secure_url } = await data.json();
          value = secure_url;
        })
        .catch((err) => alert("Une Erreur Est Arrive !"))
        .finally(() => dispatch(toggleNetworkCall()));
    }
    data = { ...data, [name]: value };
  };

  const onSubmit = async (e) => {
    e.preventDefault();

    // ========================= SEND A CODE TO THE EMAIL ADDRESS IF THERE IS ONE =====================
    if (
      Object.keys(data)
        .map((item) => item.toLocaleLowerCase())
        .includes("email")
    ) {
      dispatch(toggleNetworkCall());
      const code = Math.floor(Math.random() * 10000);
      await APIView({
        method: "POST",
        url: "send_mail/",
        host: window.EmailerHost,
        data: {
          email: [data.email],
          subject: "Mail Confirmation ( MFarm ).",
          variables: { code, app: "MFarm" },
          template: "sendcode",
        },
      })
        .then(async () => {
          const verification = await promptAsk(
            "Mettez Le Code Envoy A Ce Compte Email"
          );
          if (verification == code) {
            delete data["email_error"];
          }
        })
        .finally(() => dispatch(toggleNetworkCall()));

      // nbkassumanidieudonne@gmail.com
      if (data.email_error) {
        return alert("Verifier Votre L'Email Entr Sur Ce Formulaire !");
      }
    }
    // ===================================================================================================

    dispatch(toggleNetworkCall());
    APIViewMulti({ url, data, default_response, method: "POST" })
      .then((response) => {
        console.log(default_response, response);

        // ===============================================================================================
        if (default_response === response) {
          dispatch(
            addAlert({
              message: `Server: Une Erreur Est Survenue Lors De Reception Des Donnes !`,
              type: "danger",
            })
          );
          return;
        }

        // ===============================================================================================
        if (response.errors) {
          response.errors.forEach((item) =>
            dispatch(
              addAlert({
                message: `${item.name}: ${item.message}`,
                type: "danger",
              })
            )
          );
          return;
        }

        // ===============================================================================================
        dispatch(
          addAlert({ message: "Enregistr Avec Succs ", type: "primary" })
        );
      })
      .finally(() => dispatch(toggleNetworkCall()));
  };

  return (
    <form onChange={onChange} onSubmit={onSubmit}>
      {children}
      <div className="d-grid">
        <button className="btn btn-primary" type="button" onClick={onSubmit}>
          {button_text}
        </button>
      </div>
    </form>
  );
}

export function Login({ onSuccess = () => {} }) {
  const dispatch = useDispatch();
  const [data, setData] = useState({});
  const onChange = ({ target: { name, value } }) => {
    setData((state) => ({ ...state, [name]: value }));
  };

  const onLogin = async (e) => {
    e.preventDefault();
    dispatch(toggleNetworkCall());
    fetch(`${window.NetworkHost}/api/login/`, {
      method: "POST",
      body: JSON.stringify(data),
      headers: { "content-type": "application/json" },
    })
      .then((response) => response.json())
      .then((loginData) => {
        if (loginData.token && loginData.user?.is_admin) {
          dispatch(
            addAlert({
              message: "Connexion Avec Success !",
              type: "primary",
            })
          );
          return onSuccess(loginData.token);
        }

        if (!loginData.message && !loginData.user?.is_admin) {
          loginData.message = "Connexion Refuse ...";
        }

        dispatch(
          addAlert({
            message: loginData.message,
            type: "danger",
          })
        );
      })
      .catch((err) => {
        dispatch(
          addAlert({
            message:
              "Une Erreur Est Survenue Lors De L'Etablissement De La Connexion Au Server !",
            type: "danger",
          })
        );
      })
      .finally(() => {
        dispatch(toggleNetworkCall());
      });
  };

  return (
    <div id="login_area" className="flex">
      <form onSubmit={onLogin}>
        <div className="mb-3">
          <input
            className="form-control"
            id="user_name_login"
            placeholder="Nom D'Utilisateur"
            name="username"
            value={data.username}
            onChange={onChange}
          />
        </div>
        <div className="mb-3">
          <input
            type="text"
            className="form-control"
            id="password_login"
            placeholder="Mots De Password"
            name="password"
            value={data.password}
            onChange={onChange}
          />
        </div>
        <div className="d-grid">
          <button onClick={onLogin} className="btn btn-primary" type="button">
            Connexion
          </button>
        </div>
      </form>
    </div>
  );
}

