import React, { Component } from "react";
import { PlaceList } from "../Farm/Farm";
import EtangItem from "./EtangItem";
import { useDispatch } from "react-redux";
import { toggleSelectedEtang } from "../Redux/Store";
import { ErrorBoundnary } from "../Reuse/Reuse";
import "./Etang.css";

function EtangDetail({ data }) {
  const dispatch = useDispatch();
  const {
    place = { name: "--------------" },
    admin = { name: "--------------" },
    fish = { name: "--------------" },
    name = "--------------",
    image = "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSFptrq2UPvr6McnQ0oF2pJg6ox6IvuVy1JoQ&usqp=CAU",
    superficie = "--------------",
  } = data;
  return (
    <div id="etang_party_two">
      <img src={image} alt="Etang Image" />
      <div id="EtangViewToggleButtom" className="flex">
        <p>{name}</p>
        <i
          className="fas fa-eye"
          onClick={(e) => {
            e.preventDefault();
            dispatch(toggleSelectedEtang(data));
          }}
        ></i>
      </div>
      <div id="EtangDetail">
        <p>Poisson: {fish.name}</p>
        <p>
          Superficie: <span>{superficie}</span>
        </p>
        <p>
          Admin: <span>{admin?.name}</span>
        </p>
        <p>
          Addresse: <span>{place?.name}</span>
        </p>
      </div>
    </div>
  );
}

export default class Etang extends Component {
  state = { SelectedEtang: {} };
  FilterEtang = ({ etangs }) => etangs;
  ToggleEtang = (data) => this.setState({ SelectedEtang: data });

  render() {
    return (
      <ErrorBoundnary>
        <div id="Etang">
          <EtangItem />
          <PlaceList
            Filter={this.FilterEtang}
            onToggle={this.ToggleEtang}
            extra={{ id: "etang_party_one" }}
          />
          <EtangDetail data={this.state.SelectedEtang} />
        </div>
      </ErrorBoundnary>
    );
  }
}
