window.NetworkHost = 'http://husbandry-server.herokuapp.com';
window.EmailerHost = 'http://husbandry-server.herokuapp.com';

// window.NetworkHost = 'http://localhost:8000';
// window.EmailerHost = 'http://localhost:8000';

export async function APIViewOne(options) {
    return APIView({ ...options, url: 'view_one/' + options.url });
};

export async function APIViewMulti(options) {
    return APIView({...options, url: 'view_two/' + options.url});
};

export async function APICallbackTable(options) {
    return APIView({ ...options, url: 'callback_table/' + options.url, default_response: [] });
};

export async function APIFunctionCallback(options) {
    return APIView({ default_response: {}, ...options, url: 'callback/' + options.url });
};

export default async function APIView({host = window.NetworkHost, url= '', format="json", data= {}, method= 'GET', default_response= {}}) {
    const headers = { 'content-type': 'application/json', authorization: `Token ${sessionStorage.getItem('authtoken')}` };
    const extra_headers = { 'content-type': 'application/json' }
    
    const URL = `${host}/api/` + url;
    var RQ;
    if ( method === 'POST' ) {
        RQ = fetch(URL, { headers, body: JSON.stringify( data ), method });
    }
    
    else if ( method === 'GET' ) {
        RQ = fetch(URL, { headers, ...extra_headers, 'method': method});
    }

    else return (default_response);

    return RQ.then(async response => {
        const Data = await response.json();
        if ( !response.ok ) {
            if ( Data.detail ) {
                alert('Votre Session Est Expir !')
                return default_response;
            }
        }

        console.log(Data);
        return Data;
    }).then(data => data).catch((error) => default_response);
};