import {
  saveServerToken,
  addAlert,
  toggleNetworkCall,
} from "../Redux/Store.js";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import APIView from "../api";
import { connect } from "react-redux";
import "./Nav.css";

class Nav extends Component {
  Logout = (e) => {
    e.preventDefault();
    this.props.dispatch(toggleNetworkCall());
    APIView({
      url: "logout/",
      default_response: { message: "Deconnexion ...  " },
    })
      .then((data) => {
        this.props.dispatch(
          addAlert({ message: data.message, type: "primary" })
        );
        this.props.dispatch(saveServerToken(null));
      })
      .finally(() => this.props.dispatch(toggleNetworkCall()));
  };

  render() {
    return (
      <div id="navbar" className="flex">
        <div id="navbar_left">
          <Link to="/Farm">Ferme</Link>
          <Link to="/Champ">Champ</Link>
          <Link to="/Etang">Etang</Link>
          <Link to="/Edit">Sauvegarder</Link>
        </div>
        <div id="navbar_right">
          <p onClick={this.Logout}>
            Dconnexion <i className="fa-solid fa-arrow-right-from-bracket"></i>
          </p>
        </div>
      </div>
    );
  }
}
export default connect(null, null)(Nav);
