import { configureStore, createSlice } from "@reduxjs/toolkit";



const Utils = createSlice({
    name: 'Utils',
    initialState: {
        SelectedFarm: {},
        SelectedAnimal: {},
        SelectedField: {},
        SelectedEtang: {},
        SelectedEditForm: "",
        NetworkCall: false,
        Alerts: [],
        AuthToken: null
    },
    reducers: {
        toggleSelectedFarm: (store, { payload }) => {
            store.SelectedFarm = payload
        },


        
        toggleSelectedAnimal: (store, { payload }) => {
            store.SelectedAnimal = payload
        },


        
        toggleSelectedField: (store, { payload }) => {
            store.SelectedField = payload
        },


        
        toggleSelectedEtang: (store, { payload }) => {
            store.SelectedEtang = payload
        },


        
        toggleSelectedEditForm: (store, { payload }) => {
            store.SelectedEditForm = payload
        },


        
        addAlert: (store, { payload }) => {
            store.Alerts.push({ id: Math.floor( Math.random() * 1250000 ), ...payload})
        },


        
        removeAlert: (store, { payload }) => {
            store.Alerts = store.Alerts.filter(item => item.id !== payload)
        },




        toggleNetworkCall: (store) => {
            store.NetworkCall = !store.NetworkCall;
        },


        saveServerToken: (store, { payload }) => {
            store.AuthToken = payload;
            sessionStorage.setItem('authtoken', payload);
        }
    }
});



export const {
    toggleSelectedFarm, toggleSelectedAnimal, toggleSelectedField, toggleSelectedEtang,
    toggleSelectedEditForm, addAlert, removeAlert, toggleNetworkCall, saveServerToken
} = Utils.actions;



const Store = configureStore({
    reducer: {
        Utils: Utils.reducer
    }
});


export default Store;

